import Logo from "../images/logo.png";
import { Link, useLocation } from "react-router-dom";

function toggleNavBar() {
  const burger = document.querySelector(".burger");
  const nav = document.querySelector(".nav-links");
  const navLinks = document.querySelectorAll(".nav-links li");
  //Toggle Nav
  nav.classList.toggle("nav-active");
  //Animate Links
  navLinks.forEach((link, index) => {
    if (link.style.animation) {
      link.style.animation = "";
    } else {
      link.style.animation = `navLinkFade 0.4s ease forwards ${
        index / 7 + 0.5
      }s`;
    }
  });
  //Burger Animation
  burger.classList.toggle("toggle");
}
function closeNavbar() {
  const burger = document.querySelector(".burger");
  const nav = document.querySelector(".nav-links");
  const navLinks = document.querySelectorAll(".nav-links li");
  nav.classList.toggle("nav-active");
  burger.classList.toggle("toggle");
  //Animate Links
  navLinks.forEach((link) => {
    link.style.animation = "";
  });
}

function NavBar() {
  let location = useLocation();
  let path = location.pathname;
  return (
    <>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <nav className="fixed-top">
              <div className="logo">
                <Link to="/">
                  <img src={Logo} width="160px" alt="logo" />
                </Link>
              </div>
              <ul className="nav-links">
                <Link to="/" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${path === "/" ? "#536DFE" : "#242424"}`,
                      transition: "0.3s ease",
                    }}
                  >
                    HOME
                  </li>
                </Link>

                <Link to="/services" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${path === "/services" ? "#536DFE" : "#242424"}`,
                      transition: "0.3s ease",
                    }}
                  >
                    SERVICES
                  </li>
                </Link>
                <Link to="/testimonials" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${
                        path === "/testimonials" ? "#536DFE" : "#242424"
                      }`,
                      transition: "0.3s ease",
                    }}
                  >
                    TESTIMONIALS
                  </li>
                </Link>
                <Link to="/about" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${path === "/about" ? "#536DFE" : "#242424"}`,
                      transition: "0.3s ease",
                    }}
                  >
                    ABOUT
                  </li>
                </Link>
                <Link to="/gallery" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${path === "/gallery" ? "#536DFE" : "#242424"}`,
                      transition: "0.3s ease",
                    }}
                  >
                    GALLERY
                  </li>
                </Link>
                <Link to="/contact" onClick={closeNavbar}>
                  <li
                    style={{
                      color: `${path === "/contact" ? "#536DFE" : "#242424"}`,
                      transition: "0.3s ease",
                    }}
                  >
                    CONTACT
                  </li>
                </Link>
              </ul>
              <div className="burger" onClick={toggleNavBar}>
                <div className="line1"></div>
                <div className="line2"></div>
                <div className="line3"></div>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
}

export default NavBar;
